* {
    margin: 0;
    padding: 0;
}

.container {
    background: linear-gradient( #e1b382, #c89666);
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app-wrapper {
    background-color: #12343b;
    min-width: 450px;
    padding: 30px;
    min-height: 650px;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 3px 6px 40px #000;
    margin-bottom: 10px;
}

.header h1 {
    color: #fff;
    text-align: center;
    margin: 30px 0;
}

.task-input {
    outline: none;
    width: 260px;
    padding: 15px;
    margin-right: 25px;
    font-size: 20px;
    color: #ccc;
    background-color: #000;
    border: 1px solid #c89666;
    border-radius: 10px;
}

.button-add {
    width: 70px;
    padding: 15px 15px;
    font-size: 20px;
    border-radius: 10px;
    border: 0;
    background-color: #f1af71;
    cursor: pointer;
}

.list-item {
    display: flex;
    margin: 20px 0;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    max-height: 30px;
}

.button-complete,
.button-delete,
.button-edit {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    font-size: 28px;
    border-radius: 50%;
}

.button-complete {
    color: #ff6c6c;
    margin-right: 10px;
}

.button-edit {
    color: #e2d029;
    margin-right: 10px;
}

.button-delete {
    color: lightseagreen;
}

.complete {
    text-decoration-style: solid;
    text-decoration-line: line-through;
    text-decoration-color: #ff6c6c;
    opacity: 0.6;
}

.list {
    width: 220px;
    background-color: #12343b;
    border: none;
    font-size: 20px;
    color: white;
    padding-left: 10px;
    margin-right: 15px;
}

input:focus,
button:focus {
    outline: none;
}

@media only screen and (max-width: 768px) {
    .app-wrapper {
        min-width: 300px;
        min-height: 450px;
    }

    .task-input {
        width: 80%;
        margin-right: 0;
        margin-bottom: 15px;
    }

    .button-add {
        width: 20%;
        font-size: 16px;
        padding: 12px 12px;
    }

    .list-item {
        margin: 15px 0;
    }

    .list {
        width: 50%;
        font-size: 16px;
    }
}